import React, {useEffect, useState} from 'react';
import { CButton, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react';

import './slots.scss';
import { Link } from 'react-router-dom';


const SlotList=()=> {
  const [doctor, setDoctor] = useState([]);
  const [slot, setSlot] = useState([]);

  useEffect(() => {
   
    const clinic_id = JSON.parse(localStorage.getItem("user"));
    console.log(clinic_id._id)
    fetch(process.env.REACT_APP_API_URL+"slot/list", {
        method: 'POST', // or 'PUT'
        headers: {
            'Content-Type': 'application/json',
        }
    }).then(e=>e.json()).then((data)=>{
        console.log(data.data.filter(i=>i.clinic===clinic_id._id));
        setSlot(data.data.filter(i=>i.clinic===clinic_id._id));
    })


  }, []);
  

  return (<>
    <div className='d-flex justify-content-between align-items-center mb-3'>
        <h2 className='mb-3'>Slot List</h2>
        <Link className='btn btn-lg btn-primary' to={"/slots/add"}>Add Slots</Link>
    </div>
    <div className='arTableWrapper'>
        <div className='SlotLists'>
            <CTable striped bordered>
                <CTableHead>
                    <CTableRow>
                    <CTableHeaderCell width={100} scope="col">Slot Id</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Doctor Name</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Slot Start</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Slot End</CTableHeaderCell>

                    <CTableHeaderCell scope="col">Day</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Status</CTableHeaderCell>
                    </CTableRow>
                </CTableHead>
                <CTableBody>
                    {slot.map((i, k)=>(
                        <CTableRow key={k}>
                            <CTableDataCell scope="row">{i._id}</CTableDataCell>
                            <CTableDataCell>Dr. {!i.doctor?"": <span> {i.doctor.first_name} {" "} {i.doctor.last_name}</span> } </CTableDataCell>
                            <CTableDataCell>{i.start_time.h.toString().padStart(2, '0')}:{i.start_time.m.toString().padStart(2, '0')}</CTableDataCell>
                            <CTableDataCell>{i.end_time.h.toString().padStart(2, '0')}:{i.end_time.m.toString().padStart(2, '0')}</CTableDataCell>
                            <CTableDataCell>{i.day_name}</CTableDataCell>
                            <CTableDataCell><button className={i.status?"btn btn-success":"btn btn-primary"}>{i.status?"Active":"Pending"}</button></CTableDataCell>                   
                        </CTableRow>
                    ))}  
                    {slot.length===0&&<CTableRow><CTableDataCell className='text-center' colSpan={6}>No Slot Found.</CTableDataCell></CTableRow>}              
                </CTableBody>
            </CTable>
        </div>
    </div>
    </>
  )
}
export default SlotList;